import React, { createContext, useState } from "react";

const AppContext = createContext();
const AppContextProvider = ({ children }) => {
  let [actualizarContext, setActualizarContext] = useState({
    actualizar: false
  });
  
  console.log('ssksksks',actualizarContext)

  return (
    <AppContext.Provider value={{ actualizarContext, setActualizarContext }}>
      {children}
    </AppContext.Provider>
  );
};

export { AppContext, AppContextProvider };